import * as React from 'react'
import algoliasearch from 'algoliasearch/lite' // https://www.npmjs.com/package/algoliasearch
import debounce from 'lodash.debounce'
import isEmpty from 'lodash.isempty'
import { resolveTranslatedString } from './helpers/intl'
import {
  Spinner,
  TextInput,
  FormLabel,
  HelpText,
  Button,
  Select,
  Option,
  TextLink,
  Icon,
} from '@contentful/forma-36-react-components'
import { init, FieldExtensionSDK } from 'contentful-ui-extensions-sdk'
import { SortableContainer, SortableElement } from 'react-sortable-hoc'
import { render } from 'react-dom'
import arrayMove from 'array-move'

import Product from './components/Product'
import ListProduct from './components/ListProduct'

import '@contentful/forma-36-react-components/dist/styles.css'
import './index.scss'

type Props = {
  sdk: FieldExtensionSDK
}

type State = {
  hits: any[]
  pending: boolean
  query: string
  products: any[]
  productsView: boolean
  selectedProducts: any[]
}

const mapOrder = (target: any[], order: string[], key: string) => {
  target.sort((a, b) => {
    const A = a[key]
    const B = b[key]

    if (order.indexOf(A) > order.indexOf(B)) {
      return 1
    } else {
      return -1
    }
  })

  return target
}

const SortableProduct = SortableElement(
  ({
    product,
    removeProduct,
    warehouseId,
  }: {
    product: any
    removeProduct: any
    warehouseId: any
  }) => {
    return (
      <Product
        {...product}
        removeProduct={removeProduct}
        warehouseId={warehouseId}
      />
    )
  }
)

const SortableProductList = SortableContainer(
  ({
    products,
    removeProduct,
    gridView,
    warehouseId,
  }: {
    products: any[]
    removeProduct: any
    gridView: boolean
    warehouseId: number
  }) => {
    return (
      <div id="selectedProducts" className={gridView ? `grid` : `list`}>
        {products.map((product, index) => (
          <SortableProduct
            key={product ? product.sku : `missing_${index}`}
            index={index}
            product={product}
            removeProduct={removeProduct}
            warehouseId={warehouseId}
          />
        ))}
      </div>
    )
  }
)

export const splitFileName = (file: string) => {
  if (!file) {
    return {
      name: 'error',
      file: 'error',
    }
  }
  const extensionIndex = file.lastIndexOf('.')
  const name = file.substr(0, extensionIndex)
  const extension = file.substr(extensionIndex, file.length)
  return { name, extension }
}

export const removeDuplicates = (array: any[], key: string) => {
  const withoutDuplicates: any = []
  const uniqueObject: any = {}

  for (let i in array) {
    // Get array item key
    let uniqueId = array[i][key]

    // Use key as index, if duplicates it will be overwritten
    uniqueObject[uniqueId] = array[i]
  }

  // Loop through unique object and push into array items
  for (let i in uniqueObject) {
    withoutDuplicates.push(uniqueObject[i])
  }

  // Return array
  return withoutDuplicates
}

const isDialog = (sdk: any) => {
  if (sdk.parameters.instance.algoliaAppId === '') {
    return true
  }
  return false
}

const isOnSale = (product: any) => {
  if (!product || !product.prices) {
    return false
  }
  let isOnSale = false
  const { prices } = product
  for (const pl of Object.keys(prices)) {
    if (prices[pl].showAsOnSale) {
      isOnSale = true
    }
  }

  return isOnSale
}

const getCategory = (product: any) => {
  if (!product || !product.categories) {
    return 'No Category'
  }
  let categoryParts = []
  for (const category of product.categories) {
    categoryParts.push(category.name[category.name.length - 1])
  }

  return categoryParts.join(' / ')
}

export class App extends React.Component<Props, State> {
  client: any
  index: any
  searchInputNode: any

  state = {
    hits: [],
    pending: false,
    query: '',
    products: [],
    productsView: true,
    selectedProducts: [],
  }

  constructor(props: Props) {
    super(props)
    const { sdk } = props
    const { algoliaAppId, algoliaApiKey, algoliaIndex } = !isDialog(sdk)
      ? (sdk.parameters.instance as any)
      : (sdk.parameters.invocation as any)
    this.client = algoliasearch(algoliaAppId, algoliaApiKey)
    this.index = this.client.initIndex(algoliaIndex)
  }

  detachExternalChangeHandler: Function | null = null

  componentDidMount() {
    const { sdk } = this.props

    // Listen to value changing
    this.onValueChange()

    // Autoresize on load
    sdk.window.startAutoResizer()

    // Set selected products to value passed from entry editor when opening dialog window
    if (isDialog(sdk) && !this.state.selectedProducts.length) {
      this.setState({
        selectedProducts: sdk.parameters.invocation.fieldValue || [],
      })
    }
  }

  componentWillUnmount() {
    if (this.detachExternalChangeHandler) {
      this.detachExternalChangeHandler()
    }
  }

  onValueChange = () => {
    const { sdk } = this.props

    // Listen to value changing in entry editor
    if (sdk.field) {
      sdk.field.onValueChanged((value: string[] = []) => {
        if (value && value.length > 0) {
          const nextProducts: any[] = value.slice(0)
          const fetchSkus: string[] = []
          const { products } = this.state

          value.forEach((sku: string, i: number) => {
            const foundProduct = products.find(
              (product: any) => product && product.sku === sku
            )
            if (foundProduct) {
              nextProducts[i] = Object.assign({}, foundProduct)
            } else {
              fetchSkus.push(sku)
            }
          })

          if (fetchSkus.length) {
            // Make sure filters for SKU:s are activated in Algolia
            const filters = fetchSkus.map(sku => `sku:${sku}`).join(' OR ')
            this.index
              .search('', {
                filters,
                hitsPerPage: fetchSkus.length,
              })
              .then((props: { hits: any[] }) => {
                const hits = props.hits
                let fetchIds: number[] = []
                for (const hit of hits) {
                  const index = value.indexOf(hit.sku)
                  nextProducts[index] = hit
                  fetchIds[index] = hit.id
                }
                // Filter out null, Centra can’t handle it
                fetchIds = fetchIds.filter((id) => !!id)
                // Store these in case something goes awry
                this.setState({ products: nextProducts })
                const {
                  centraBaseUrl,
                  centraToken,
                }: any = sdk.parameters.installation
                fetch(`${centraBaseUrl}/products`, {
                  method: 'POST',
                  body: JSON.stringify({
                    products: fetchIds,
                    pricelist: 'all',
                    market: 'all',
                  }),
                  headers: {
                    'API-Authorization': centraToken,
                  },
                })
                  .then((response) => response.json())
                  .then((response) => {
                    const products = response.products

                    const decoratedProducts = nextProducts.slice(0)
                    for (const product of nextProducts) {
                      const sku = product.sku
                      const index = value.indexOf(sku)
                      const centraProduct = products.find((p: any) => p && p.sku === sku)
                      if (centraProduct) {
                        decoratedProducts[index] = {
                          ...Object.assign({}, nextProducts[index]),
                          isOnSale: isOnSale(centraProduct),
                          category: getCategory(centraProduct),
                          itemTable: centraProduct.itemTable,
                          items: centraProduct.items,
                        }
                      } else {
                        decoratedProducts[index] = Object.assign({}, nextProducts[index])
                      }
                    }
                    this.setState({ products: decoratedProducts })
                  })
                  .catch((error) => {
                    console.error(error)
                  })
              })
              .catch((error: any) => {
                sdk.notifier.error(error.message)
              })
          } else {
            this.setState({ products: nextProducts })
          }
        } else {
          this.setState({ products: [] })
        }
      })
    }
  }

  parseProductData = (product: any, locale: string) => {
    const { sdk } = this.props

    // Use default locale for now
    const defaultLocale =
      sdk && sdk.locales && sdk.locales.default ? sdk.locales.default : null

    const {
      AlgoliaNameProperty,
      AlgoliaSkuProperty,
      AlgoliaImageProperty,
      imageStringParser,
    } = !isDialog(sdk)
      ? (sdk.parameters.instance as any)
      : (sdk.parameters.invocation as any)

    let productObject

    if (product[AlgoliaSkuProperty] && defaultLocale) {
      // For products we can find in the Algolia database,
      // `product` will be an object.
      let image = ''

      // Set image url
      if (product.hasOwnProperty(AlgoliaImageProperty)) {
        const { name, extension } = splitFileName(product[AlgoliaImageProperty])
        image =
          name !== 'error'
            ? imageStringParser
                .replace('${name}', name)
                .replace('${extension}', extension)
            : ''
      }

      const { category, isOnSale, itemTable, items } = product

      productObject = {
        name: resolveTranslatedString(
          product[AlgoliaNameProperty],
          '',
          defaultLocale
        ),
        image: image,
        sku: product[AlgoliaSkuProperty],
        category,
        isOnSale,
        itemTable,
        items,
      }
    } else {
      // For products we can’t find, `product` will be the original string sku.
      // This allows us to move or remove the product.
      productObject = {
        name: 'Missing product',
        image: '',
        sku: product,
      }
    }

    return productObject
  }

  openDialog = () => {
    const { sdk } = this.props
    const {
      algoliaAppId,
      algoliaApiKey,
      algoliaIndex,
      AlgoliaNameProperty,
      AlgoliaSkuProperty,
      AlgoliaImageProperty,
      imageStringParser,
    } = !isDialog(sdk)
      ? (sdk.parameters.instance as any)
      : (sdk.parameters.invocation as any)

    // Open dialog and pass algolia parameters and added products
    sdk.dialogs
      .openExtension({
        width: 'large',
        title: 'Select products',
        shouldCloseOnOverlayClick: true,
        shouldCloseOnEscapePress: true,
        allowHeightOverflow: true,
        minHeight: 640,
        parameters: {
          algoliaAppId,
          algoliaApiKey,
          algoliaIndex,
          AlgoliaNameProperty,
          AlgoliaSkuProperty,
          AlgoliaImageProperty,
          imageStringParser,
          fieldLocale: sdk.field.locale || '',
          fieldValue: sdk.field.getValue() || [],
        },
      })
      .then(data => {
        // Add selected products when dialog is closed and data has been passed
        if (data && Array.isArray(data)) {
          this.addProducts(data)
        }
      })
  }

  closeDialog = (data: any) => {
    // Close dialog and pass data when using the "Add products" button
    this.props.sdk.close(data)
  }

  selectProduct = (sku: string) => {
    const { sdk } = this.props

    if (!sku) {
      sdk.notifier.error(`No product was selected`)
      return
    }

    // Check if product is already selected, otherwise add it to array
    const selectedProducts = (this.state.selectedProducts as any) || []
    if (selectedProducts.indexOf(sku) !== -1) {
      sdk.notifier.error(`Product ${sku} is already selected`)
    } else {
      this.setState({ selectedProducts: [...selectedProducts, sku] })
    }
  }

  deselectProduct = (sku: string) => {
    const { sdk } = this.props

    if (!sku) {
      sdk.notifier.error(`No product was deslected`)
      return
    }

    // Check if product is selected and remove from array
    const selectedProducts = (this.state.selectedProducts as any) || []
    const index = selectedProducts.indexOf(sku)
    if (index === -1) {
      sdk.notifier.error(`Product ${sku} not found in list`)
    } else {
      selectedProducts.splice(index, 1)
      this.setState({ selectedProducts: selectedProducts })
    }
  }

  deselectAllProducts = (skus: string[]) => {
    const { sdk } = this.props
    const { selectedProducts } = this.state

    if (!skus.length) {
      sdk.notifier.error(`No new products was deselected`)
      return
    }

    // Deselect all search hit products from selected products
    let newProducts: any =
      selectedProducts.filter((sku: string) => skus.indexOf(sku) === -1) || []
    this.setState({ selectedProducts: [...newProducts] })
  }

  selectAllProducts = (skus: string[]) => {
    const { sdk } = this.props
    const { selectedProducts } = this.state

    if (!skus.length) {
      sdk.notifier.error(`No new products was selected`)
      return
    }

    // Select all search hit products
    let newProducts: any =
      skus.filter((sku: string) => selectedProducts.indexOf(sku) === -1) || []

    const select = (newProducts: string[]) => {
      this.setState({ selectedProducts: [...newProducts, ...selectedProducts] })
    }

    if (newProducts.length > 9) {
      sdk.dialogs
        .openConfirm({
          title: 'Confirm',
          message: `Are you sure you want to select ${skus.length} products?`,
          intent: 'positive',
          confirmLabel: 'Yes',
          cancelLabel: 'No',
        })
        .then((result: boolean) => {
          if (result) {
            select(newProducts)
          }
        })
    } else {
      select(newProducts)
    }
  }

  addProducts = (skus: string[]) => {
    const { sdk } = this.props
    const value = sdk.field.getValue()

    if (JSON.stringify(skus) === JSON.stringify(value)) {
      sdk.notifier.error(`No new products was added`)
      return
    }
    // Add selected products passed from the dialog
    if (skus.length === 0) {
      sdk.field.setValue([])
      sdk.notifier.error(`No products was added`)
    } else {
      sdk.field.setValue([...skus])
      sdk.notifier.success(
        `Successfully added product${skus.length > 0 ? 's' : ``}: ${skus.join(
          ', '
        )}`
      )
    }
  }

  removeProduct = (sku: string) => {
    const { sdk } = this.props
    const value = sdk.field.getValue()
    const index = value.indexOf(sku)

    // Remove product from list when clicking x button
    if (index !== -1) {
      value.splice(index, 1)
      sdk.field.setValue(value)
      sdk.notifier.success(`Successfully removed product ${sku}`)
    } else {
      sdk.notifier.error(`Product ${sku} not found in value`)
    }
  }

  removeAllProducts = () => {
    const { sdk } = this.props

    // Remove all products from list after accepting dialog confirmation
    sdk.dialogs
      .openConfirm({
        title: 'Remove all products',
        message:
          'Are you sure you want to remove all selected products from your list?',
        intent: 'negative',
        confirmLabel: 'Yes',
        cancelLabel: 'No',
      })
      .then(result => {
        if (result) {
          sdk.field.setValue([])
          sdk.notifier.success(`Successfully removed all products`)
        }
      })
  }

  onSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.searchInputNode = e.target
    const query = e.target.value
    if (!query) {
      this.clearSearch()
    } else {
      this.setState(
        {
          pending: true,
          query,
        },
        this.doSearch
      )
    }
  }

  onSearchSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
  }

  onKeyUp = (e: React.KeyboardEvent) => {
    // ESC aborts search
    if (e.keyCode === 27) {
      this.clearSearch()
    }
  }

  clearSearch = () => {
    this.setState({
      hits: [],
      pending: false,
      query: '',
    })
    if (this.searchInputNode) {
      this.searchInputNode.focus()
    }
  }

  doSearch = debounce(() => {
    let { query = '' } = this.state
    let filters = ''
    let skuOrder: string[] = []
    if (query) {
      if (query.includes(',')) {
        skuOrder = query
          .split(',')
          .filter(element => !!element)
          .map((sku) => sku.replace(/\s/g, ''))
        filters = skuOrder.map(sku => `sku:${sku}`).join(' OR ')
        query = ''
      }

      // Send search to Algolia
      this.index
        .search(query, {
          hitsPerPage: 1000,
          filters,
        })
        .then(({ hits }: { hits: any[] }) => {
          // Only use products in search result
          const hitsWithSku = hits.filter(hit => hit.hasOwnProperty('sku'))
          // Remove duplicate skus
          const hitsWithoutDuplicates = removeDuplicates(hitsWithSku, 'sku')
          // Order array if filters were used
          if (filters) {
            const sortedHitsWithoutDuplicates = mapOrder(
              hitsWithoutDuplicates,
              skuOrder,
              'sku'
            )
            this.setState({ pending: false, hits: sortedHitsWithoutDuplicates })
          } else {
            this.setState({ pending: false, hits: hitsWithoutDuplicates })
          }
        })
        .catch((error: any) => {
          this.props.sdk.notifier.error(error.message)
          console.log(error) /* eslint-disable-line */
        })
    } else {
      this.clearSearch()
    }
  }, 600)

  onSortEnd = ({ oldIndex, newIndex }: { oldIndex: any; newIndex: any }) => {
    const { sdk } = this.props
    const value = sdk.field.getValue()
    sdk.field.setValue(arrayMove(value, oldIndex, newIndex))
  }

  changeProductsView = (event: any) => {
    const gridView = event.target.value === 'grid' ? true : false
    this.setState({ productsView: gridView })
  }

  render = () => {
    const { sdk } = this.props

    const {
      hits = [],
      pending = false,
      query = '',
      products = [],
      productsView = true,
      selectedProducts = [],
    } = this.state

    // Set value to passed parameter if extension is opened in dialog
    const value =
      (!isDialog(sdk) && sdk.field.getValue()) ||
      (isDialog(sdk) && (sdk.parameters.invocation.fieldValue as any)) ||
      []
    // Get field locale
    const locale =
      (!isDialog(sdk) && sdk.field.locale) ||
      (isDialog(sdk) && (sdk.parameters.invocation.fieldLocale as string)) ||
      ''

    const newHits =
      hits
        .map((hit: any) => hit.sku)
        .filter((sku: string) => selectedProducts.indexOf(sku) === -1) || []
    const selectedHits =
      hits
        .map((hit: any) => hit.sku)
        .filter((sku: string) => selectedProducts.indexOf(sku) !== -1) || []

    const warehouseId = parseInt(sdk.parameters.installation.warehouseId)

    return (
      <div id="wrapper">
        <div id="content">
          {isDialog(sdk) ? (
            <div id="dialogWrapper">
              <div id="dialogTop">
                <div id="tools">
                  <div id="search">
                    <form onSubmit={this.onSearchSubmit} action=".">
                      <FormLabel htmlFor="search">
                        Search for products:
                      </FormLabel>
                      <div id="searchField">
                        <TextInput
                          name="search"
                          autoCapitalize="off"
                          autoComplete="off"
                          autoCorrect="off"
                          autoFocus={false}
                          onChange={this.onSearchChange}
                          onKeyUp={this.onKeyUp}
                          placeholder={'Type to search for products'}
                          spellCheck={false}
                          type="text"
                          value={query}
                        />
                        {pending ? (
                          <div className="spinner">
                            <Spinner />
                          </div>
                        ) : hits.length ? (
                          <button onClick={this.clearSearch} className="close">
                            <Icon color="primary" icon="Close" size="small" />
                          </button>
                        ) : null}
                      </div>
                      <HelpText>
                        You can search for a comma-separated list of SKUs.
                        Example: <code>11223, 11224, 11225</code>
                      </HelpText>
                    </form>
                  </div>
                </div>
              </div>

              <div id="dialogContent">
                {hits.length ? (
                  <div id="availableProducts">
                    <div className="infoBar">
                      <HelpText className="infoText">
                        Found {hits.length} products:
                      </HelpText>
                      <div className="controlGroup">
                        <div className="rightControls">
                          <TextLink
                            disabled={!selectedHits.length}
                            onClick={() =>
                              this.deselectAllProducts(selectedHits)
                            }
                          >
                            <Icon
                              color="primary"
                              icon="Close"
                              size="small"
                              style={{
                                verticalAlign: 'middle',
                                marginRight: '4px',
                                marginBottom: '2px',
                              }}
                            />
                            Deselect all
                          </TextLink>
                          <TextLink
                            disabled={!newHits.length}
                            onClick={() => this.selectAllProducts(newHits)}
                          >
                            <Icon
                              color="primary"
                              icon="CheckCircle"
                              size="small"
                              style={{
                                verticalAlign: 'middle',
                                marginRight: '4px',
                                marginBottom: '2px',
                              }}
                            />
                            Select all
                          </TextLink>
                        </div>
                      </div>
                    </div>
                    <ul>
                      {hits.map((algoliaProduct: any) => {
                        const product = algoliaProduct
                          ? this.parseProductData(algoliaProduct, locale)
                          : null

                        return (
                          product && (
                            <ListProduct
                              key={`product-${product.sku}`}
                              {...product}
                              onClick={() => this.selectProduct(product.sku)}
                              added={
                                selectedProducts.indexOf(product.sku) !== -1
                              }
                              deselectProduct={this.deselectProduct}
                            />
                          )
                        )
                      })}
                    </ul>
                  </div>
                ) : (
                  <div id="dialogMessage">
                    <HelpText>
                      Search for products you want to add to your list
                    </HelpText>
                  </div>
                )}
              </div>

              <div id="dialogBottom">
                <div id="dialogControls">
                  <div className="controlGroup">
                    <Button
                      disabled={
                        JSON.stringify(value) ===
                        JSON.stringify(selectedProducts)
                          ? true
                          : false
                      }
                      buttonType="positive"
                      onClick={() => this.closeDialog(selectedProducts)}
                    >
                      {`Add products ${
                        selectedProducts && selectedProducts.length > 0
                          ? `[${selectedProducts.length}]`
                          : ``
                      }`}
                    </Button>
                    <Button
                      buttonType="muted"
                      onClick={() => this.closeDialog(null)}
                    >
                      Cancel
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div id="listContent">
              <div className="controlGroup">
                <TextLink onClick={() => this.openDialog()}>
                  <Icon
                    color="primary"
                    icon="Plus"
                    size="small"
                    style={{
                      verticalAlign: 'middle',
                      marginRight: '4px',
                      marginBottom: '2px',
                    }}
                  />
                  Add products
                </TextLink>
                <TextLink
                  disabled={!products.length}
                  onClick={() => this.removeAllProducts()}
                >
                  <Icon
                    color="primary"
                    icon="Close"
                    size="small"
                    style={{
                      verticalAlign: 'middle',
                      marginRight: '4px',
                      marginBottom: '2px',
                    }}
                  />
                  Remove all products
                </TextLink>
                <div className="rightControls">
                  <Select
                    onChange={event => this.changeProductsView(event)}
                    isDisabled={!products.length}
                  >
                    <Option value="grid">Grid</Option>
                    <Option value="list">List</Option>
                  </Select>
                </div>
              </div>
              <SortableProductList
                products={products.map(product =>
                  product ? this.parseProductData(product, locale) : null
                )}
                removeProduct={this.removeProduct}
                onSortEnd={this.onSortEnd}
                axis={'xy'}
                lockToContainerEdges={true}
                helperContainer={() =>
                  document.getElementById('selectedProducts') || document.body
                }
                gridView={productsView}
                warehouseId={warehouseId}
              />
            </div>
          )}
        </div>
      </div>
    )
  }
}

init(sdk => {
  render(
    <App sdk={sdk as FieldExtensionSDK} />,
    document.getElementById('root')
  )
})

