import React, { useState, useEffect, useRef } from 'react'

type Props = {
  itemTable: Record<string, unknown>
  items: Array<Record<string, unknown>>
  setShowSizes: (show: boolean) => void
  warehouseId: number
}

const sizeMap = {
  'X Small': 'XS',
  Small: 'S',
  Medium: 'M',
  Large: 'L',
  'X Large': 'XL',
  'XX Large': 'XXL',
}

const SizeTable = ({ itemTable, items, setShowSizes, warehouseId }: Props): JSX.Element => {
  const [hover, setHover] = useState(false)

  const getStock = (rowIndex: number, colIndex: number) => {
    let stock = '-'
    if (items) {
      const item = items.find(
        ({ itemTableX, itemTableY }) => {
          return itemTableX === colIndex && itemTableY === rowIndex
        }
      )

      if (item) {
        const warehouse =
          item && item.warehouses
            ? item.warehouses.find(({ warehouse }) => warehouse === warehouseId)
            : null
        if (warehouse) {
          stock = warehouse.stock
        }
      }
    }

    return stock
  }

  return (
    <div
      className="sizeTable"
    >
      <button onClick={() => setShowSizes(false)} className="close">
        ×
      </button>

      { items && (
        <table className="sizes">
          <thead>
            <tr>
              <th></th>
              {itemTable && itemTable.x.map((colName) => {
                return (
                  <th key={`head_${colName}`}>{sizeMap[colName] || colName}</th>
                )
              })}
            </tr>
          </thead>
          <tbody>
          {itemTable && itemTable.y.length > 0 && itemTable.y.map((row, rowIndex) => {
            return (
              <tr key={row}>
                <th>{row}</th>
                {itemTable && itemTable.x.map((colName, colIndex) => {
                  return (
                    <td key={`body_${colName}`}>{getStock(rowIndex, colIndex)}</td>
                  )
                })}
              </tr>
            )
          })}
          {itemTable && itemTable.y.length === 0 && (
            <tr key={'non-jeans'}>
              <th></th>
              {itemTable && itemTable.x.map((colName, colIndex) => {
                return (
                  <td key={`body_${colName}`}>{getStock(0, colIndex)}</td>
                )
              })}
            </tr>
            )
          }
          </tbody>
        </table>
      )}
      { !items && (
        <div>No data :(</div>
      )}
      <button onClick={() => setShowSizes(false)} className="closeBg" />
    </div>
  )
}

export default SizeTable
