export type TranslatedString =
  | string
  | {
      [locale: string]: string
    }

export const resolveTranslatedString = (
  stringObject: TranslatedString,
  locale: string,
  defaultLocale: string
): string => {
  if (!stringObject) {
    return 'Missing translation'
  }
  if (typeof stringObject === 'string') {
    return stringObject
  }
  if (stringObject[locale]) {
    return stringObject[locale]
  }
  return stringObject[defaultLocale]
}
