import React, { useState } from 'react'
import { Icon } from '@contentful/forma-36-react-components'

type Props = {
  name: string
  image: string
  sku: string
  onClick?: (e: React.MouseEvent) => any
  added?: boolean
  deselectProduct: (sku: string) => void
}

const noop = () => {}

const ListProduct = ({
  name,
  image,
  sku,
  onClick,
  added,
  deselectProduct,
}: Props): JSX.Element | null => {
  const [hover, setHover] = useState(false)
  return (
    <li
      className={`list-product${added ? ' added' : ''}`}
      onClick={onClick && !added ? onClick : noop}
    >
      <div className="image">{image ? <img src={image} /> : null}</div>
      <div className="info">
        <span className="name">{name}</span>
        <span className="sku">{sku}</span>
      </div>
      {added ? (
        <div
          className="checkmark"
          onMouseEnter={() => setHover(true)}
          onMouseLeave={() => setHover(false)}
        >
          {hover ? (
            <button
              className="remove"
              onClick={() => {
                setHover(false)
                deselectProduct(sku)
              }}
            >
              <span>Deselect</span>
              <Icon icon="CloseTrimmed" size="tiny" color="negative" />
            </button>
          ) : (
            <div className="added">
              <span>Selected</span>
              <Icon icon="CheckCircleTrimmed" size="tiny" color="positive" />
            </div>
          )}
        </div>
      ) : null}
    </li>
  )
}

export default ListProduct
