import SizeTable from './SizeTable'

type Props = {
  category: string
  image: string
  isOnSale: boolean
  itemTable: Record<string, unknown>
  items: Array<Record<string, unknown>>
  name: string
  removeProduct: (sku: string) => any
  sku: string
  warehouseId: number
}

const Product = ({
  category,
  image,
  isOnSale,
  itemTable,
  items,
  name,
  removeProduct,
  sku,
  warehouseId,
}: Props): JSX.Element => {
  const [hover, setHover] = useState(false)
  const [showSizes, setShowSizes] = useState(false)

  // const setSizesVisibility = (show: boolean) => {
  //   setShowSizes(show)
  // }

  return (
    <div
      className="product"
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
    >
      <div className="productContent">
        <div className="image">
          {image ? <img src={image} /> : <div className="placeholder" />}
        </div>
        <div className="info">
          <span className="title">{name}</span>
          <span className="sku">{sku}</span>
          <span className="sale">{isOnSale ? <span>Sale</span> : ''}</span>
          <span className="category">{category}</span>
          {showSizes && (
            <SizeTable
              itemTable={itemTable}
              items={items}
              setShowSizes={setShowSizes}
              warehouseId={warehouseId}
            />
          )}
        </div>
        {hover ? (
          <>
            <button onClick={() => removeProduct(sku)} className="delete">
              ×
            </button>
            <button onClick={() => setShowSizes(true)} className="showSizes">
              Stock status
            </button>
          </>
        ) : null}
      </div>
    </div>
  )
}

export default Product
